import React, { useEffect, useState } from 'react';
import { Cart } from '../../interfaces/Cart';
import { OrderItem } from '../../interfaces/OrderItem';
import Card from '../../components/Card/Card';

export default () => {
    const [cart, setCart] = useState<any>({});

    useEffect(() => {
        setCart(JSON.parse(localStorage.getItem('cart') || "{}"));
    }, []);

    const handleChange = ({ target }: React.ChangeEvent<HTMLInputElement>) => {
        cart[target.name] = target.value;
        setCart(cart);
    }

    return <div className='row remove-margin'>
        <div className='col-xs-12 col-md-offset-2 col-md-8 col-lg-offset-3 col-lg-6'>

            <Card title="Carrinho" subtitle="">
                <div className="body">
                    <table className='table'>
                        <thead>
                            <tr>
                                <th>Produto</th>
                                <th>Valor</th>
                                <th>#</th>
                            </tr>
                        </thead>
                        <tbody>
                            {cart.itens && cart.itens.map((item: OrderItem) => <tr>
                                <td><span className='font-bold'>{item.quantity}</span> - {item.product.name}</td>
                                <td>{(item.quantity * ((item.product.price || 0) / 100)).toFixed(2).replace('.', ',')}</td>
                                <td style={{ width: '95px' }}>
                                    <div className='btn-group' role='group'>
                                        <button className='btn btn-sm btn-primary'><i className='fa fa-plus' /></button>
                                        <button className='btn btn-sm btn-danger'><i className='fa fa-minus' /></button>
                                    </div>
                                </td>
                            </tr>)}
                        </tbody>
                    </table>
                </div>
            </Card>
            <Card title="Entrega" subtitle="">
                <div className="body">

                </div>
            </Card>
            <Card title="Pagamento" subtitle="">
                <div className="body">
                    <h4>Como será realizado o pagamento?</h4>

                    <div className="row m-t-20">
                        <div className="col-xs-12 col-md-4 col-lg-2">
                            <input type="radio" id="money" name="paymentType" value="money" onChange={handleChange} />
                            <label htmlFor="money">Dinheiro</label>
                        </div>
                        <div className="col-xs-12 col-md-4 col-lg-2">
                            <input type="radio" id="creditCard" name="paymentType" value="creditCard" onChange={handleChange} />
                            <label htmlFor="creditCard">Cartão de Crédito</label>
                        </div>
                        <div className="col-xs-12 col-md-4 col-lg-2">
                            <input type="radio" id="debitCard" name="paymentType" value="debitCard" onChange={handleChange} />
                            <label htmlFor="debitCard">Cartão de Débito</label>
                        </div>
                    </div>

                    <button className='btn btn-block bg-blue'>Finalizar o Pedido</button>
                </div>
            </Card>
        </div>
    </div>
}